import Swiper from "swiper";
import "swiper/swiper-bundle.css";

export default (settings = {}) => {
  return {
    swiper: null,

    /**
     * Initialize the carousel and Swiper instance
     */
    async init() {
      await this.initializeSwiper(settings);
    },

    /**
     * Initialize Swiper carousel with custom settings
     */
    initializeSwiper(customSettings) {
      const defaultSettings = {
        loop: true,
        slidesPerView: 1,
        spaceBetween: 10,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        },
      };

      const swiperSettings = { ...defaultSettings, ...customSettings };

      // Ensure Swiper modules are imported dynamically based on settings
      if (customSettings.pagination) {
        swiperSettings.modules = swiperSettings.modules || [];
        swiperSettings.modules.push(Swiper.Pagination);
      }

      if (customSettings.navigation) {
        swiperSettings.modules = swiperSettings.modules || [];
        swiperSettings.modules.push(Swiper.Navigation);
      }

      this.swiper = new Swiper(".swiper-container", swiperSettings);
    },
  };
};
